
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import bheader from "@/components/header/header-b";
export default {
name: "seeProgram",
data() {
return {
pro_id: this.$route.query.pro_id,
tabData:['期数','时间','类别','金额','状态'],
listData:[],
proStatus:[]
}

},
components: {
bheader,XButton, Box,Confirm,ConfirmPlugin,ToastPlugin
},
created() {
this.$axios.post('/api/api/seeProgram',`data=${JSON.stringify({pro_id: this.pro_id})}`)
.then(res=>{
if(res.code==1){
console.log(res)
this.listData = res.result.repayList;
this.proStatus = res.result.repayProgram;
}else{
mui.toast(res.msg)
}
})
},
mounted() {

},
methods: {
endProgram(){
const _this = this;
_this.$vux.confirm.show({
title: '确认终止该任务？',
onCancel () {
},
onConfirm () {
_this.$axios.post('/api/api/endProgram',`data=${JSON.stringify({pro_id: _this.pro_id})}`)
.then(res=>{
mui.toast(res.msg)
if(res.code==1){
_this.$router.go(-1);
}
})
}
})
}
}
}



